/* From uiverse.io by @gagan-gv */
.btn-follow-me {
position: relative;
display: flex;
overflow: hidden;
cursor: pointer;
width: 150px;
height: 50px;
background-color: #eeeeed;
border-radius: 80px;
border: none;

top: 1.8rem;
left: 1rem;

padding: 0 80px;
transition: all .2s ease;
justify-content: center;
align-items: center;

}

.btn-follow-me:hover {
-webkit-transform: scale(1.1);
        transform: scale(1.1);
}

.btn-follow-me span {
position: absolute;
z-index: 99;
width: 300px;
height: 50px;
border-radius: 80px;
font-family: 'Courier New', Courier, monospace;
font-weight: 600;
font-size: 17px;
text-align: center;
line-height: 50px;
letter-spacing: 2px;
color: #eeeeed;
background-color: #1f1f1f;
padding: 0 10px;
transition: all 1.2s ease;
}

.btn-follow-me .container {
display: flex;
width: 150px;
border-radius: 80px;
line-height: 50px;
}

.btn-follow-me i {
padding: 0 5px;
opacity: 0;
}

.btn-follow-me .container i:nth-of-type(1) {
transition-delay: 0.65s;
}

.btn-follow-me .container i:nth-of-type(2) {
transition-delay: 0.8s;
}

.btn-follow-me .container i:nth-of-type(3) {
transition-delay: 0.5s;
}

.btn-follow-me:hover span {
opacity: 0;
}

.btn-follow-me:hover i {
opacity: 1;
}

/* From uiverse.io by @pedertanberg */
.loader {
display: inline-block;
position: relative;
width: 80px;
height: 80px;
-webkit-transform: rotate(45deg);
        transform: rotate(45deg);
-webkit-transform-origin: 40px 40px;
        transform-origin: 40px 40px;
}

.loader div {
top: 32px;
left: 32px;
position: absolute;
width: 32px;
height: 32px;
background: red;
-webkit-animation: loader45 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
        animation: loader45 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
}

.loader div:after,
.loader div:before {
content: " ";
position: absolute;
display: block;
width: 32px;
height: 32px;
background: red;
}

.loader div:before {
left: -24px;
border-radius: 50% 0 0 50%;
}

.loader div:after {
top: -24px;
border-radius: 50% 50% 0 0;
}

@-webkit-keyframes loader45 {
0% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}

5% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

39% {
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
}

45% {
    -webkit-transform: scale(1);
            transform: scale(1);
}

60% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}

100% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}
}

@keyframes loader45 {
0% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}

5% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

39% {
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
}

45% {
    -webkit-transform: scale(1);
            transform: scale(1);
}

60% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}

100% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}
}


