/* From uiverse.io by @adamgiebl */
.cssbuttons-io-button {
    background: #A370F0;
    color: white;
    font-family: var(--font-family);
    padding: 0.35em;
    padding-left: 1.2em;
    font-size: 17px;
    font-weight: 500;
