/* From uiverse.io by @adamgiebl */
.btn-upload {
    font-family: var(--font-family);
    -webkit-transform: rotate(-25deg) skew(25deg);
            transform: rotate(-25deg) skew(25deg);
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    position: relative;
    list-style: none;
    width: 100px;
    height: 32px;
    border: none;
    background: transparent;
    font-family: inherit;

    margin-top: 4rem;
    margin-left: 22rem;
}

.btn-upload:before {
content: '';
position: absolute;
bottom: -13px;
left: -5px;
width: 100%;
height: 10px;
background: #2a2a2a;
-webkit-transform: skewX(-41deg);
        transform: skewX(-41deg);
}

.btn-upload:after {
content: '';
position: absolute;
top: 5px;
left: -9px;
width: 9px;
height: 110%;
background: #2a2a2a;
-webkit-transform: skewY(-49deg);
        transform: skewY(-49deg);
}

.btn-upload span {
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 110%;
background: #2a2a2a;
background: var(--gradient-text);
color: #fff;
font-size: 25px;
transition: 1.1s ease-out;
}

.btn-upload:hover span {
z-index: 1000;
transition: .3s;
background: var(--gradient-text);
color: #fff;
}

.btn-upload:hover span:nth-child(5) {
-webkit-transform: translate(40px, -40px);
        transform: translate(40px, -40px);
opacity: 1;
}

.btn-upload:hover span:nth-child(4) {
-webkit-transform: translate(30px, -30px);
        transform: translate(30px, -30px);
opacity: .8;
}

.btn-upload:hover span:nth-child(3) {
-webkit-transform: translate(20px, -20px);
        transform: translate(20px, -20px);
opacity: .6;
}

.btn-upload:hover span:nth-child(2) {
-webkit-transform: translate(10px, -10px);
        transform: translate(10px, -10px);
opacity: .4;
}

.btn-upload:hover span:nth-child(1) {
-webkit-transform: translate(0px, 0px);
        transform: translate(0px, 0px);
opacity: .2;
}

.btn-upload:active span:nth-child(5) {
-webkit-transform: translate(20px, -20px);
        transform: translate(20px, -20px);
opacity: 1;
}

.btn-upload:active span:nth-child(4) {
-webkit-transform: translate(15px, -15px);
        transform: translate(15px, -15px);
}

.btn-upload:active span:nth-child(3) {
-webkit-transform: translate(10px, -10px);
        transform: translate(10px, -10px);
}

.btn-upload:active span:nth-child(2) {
-webkit-transform: translate(5px, -5px);
        transform: translate(5px, -5px);
}

.btn-upload:active span:nth-child(1) {
-webkit-transform: translate(0px, 0px);
        transform: translate(0px, 0px);
}

.btn-upload:nth-child(1):hover span {
background: #52E19F;
}


